html,
body {
  cursor: url('../../images/skeleton-cursor.png') 8 0, auto;
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  background-color: #1a0f00;
  background-image: url('../../images/Background.png');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

/* Main container */
.Grimoire {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
  image-rendering: pixelated;
  font-family: 'Press Start 2P', monospace;
  color: #f3e9d2;
  text-align: center;
}

.grimoire-title,
.title-shifted {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10001;
  font-size: clamp(1.5rem, 3vw, 2.5rem);
  font-family: 'Press Start 2P', monospace;
  color: #f3e9d2;
  text-shadow: 2px 2px #000;
  pointer-events: none;
}

.grimoire-title {
  top: 0%;
}

.title-shifted {
  top: -30%;
}

/* Altar layout */
.altar {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Candle image styling */
.candle-img {
  width: 50%;
  height: auto;
  image-rendering: pixelated;
  z-index: 2;
}

.candle-wrapper {
  width: 20%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

/* Glow effect */
.candle-wrapper::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 20%;
  height: 10%;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(255, 255, 150, 0.8) 0%, rgba(255, 200, 0, 0.4) 60%, transparent 100%);
  animation: candleGlow 1.5s infinite ease-in-out;
  pointer-events: none;
  z-index: 1;
}

@keyframes candleGlow {
  0%, 100% {
    opacity: 0.6;
    transform: translateX(-50%) scale(1);
    box-shadow: 0 0 6px rgba(255, 255, 150, 0.6), 0 0 12px rgba(255, 200, 0, 0.4);
  }
  50% {
    opacity: 1;
    transform: translateX(-50%) scale(1.2);
    box-shadow: 0 0 10px rgba(255, 255, 150, 0.8), 0 0 20px rgba(255, 200, 0, 0.6);
  }
}

/* Book image styling */
.book-img,
.book-img-hidden {
  width: 40vw;
  height: auto;
  image-rendering: pixelated;
  filter: drop-shadow(2px 2px 0 #000) drop-shadow(-2px -2px 0 #000) brightness(1);
  transition: filter 0.3s ease;
}

.book-img-hidden {
  visibility: hidden;
}

.book-img:hover {
  filter: drop-shadow(2px 2px 0 #000) drop-shadow(-2px -2px 0 #000) brightness(1.2);
}

/* Optional content below the altar */
.content {
  margin-top: 20%;
  max-width: 80%;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: url('../../images/GrimoireOpenedBook.png');
  background-repeat: no-repeat;
  background-position: center;
  background-blend-mode: darken;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: riseFromBook 0.6s ease-out;
  overflow-y: auto;
}

@keyframes riseFromBook {
  0% {
    transform: scale(0.5) translateY(100px);
    opacity: 0;
  }
  100% {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
}

.modal-content {
  background: rgba(0, 0, 0, 0.6);
  width: 90vw;
  max-width: 1200px;
  padding: 2rem;
  border-radius: 8px;
  text-align: center;
  color: #f3e9d2;
  font-family: 'Press Start 2P', monospace;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.modal-menu {
  background: rgba(0, 0, 0, 0.8);
  color: #f3e9d2;
  font-family: 'Press Start 2P', monospace;
  padding: 1rem;
  box-shadow: 2px 0 6px #000;
  image-rendering: pixelated;
  transition: width 0.3s ease;
  overflow: hidden;
  width: 20%;
  font-size: clamp(1rem, 2vw, 1.5rem);
}

.modal-menu button,
.modal-menu li button {
  font-size: inherit;
}

.menu-toggle {
  background: none;
  border: none;
  color: #f3e9d2;
  font-size: 1.25rem;
  cursor: pointer;
  margin-bottom: 1rem;
}

.modal-menu ul {
  list-style: none;
  padding: 0;
}

.modal-menu li {
  margin-bottom: 1rem;
}

.modal-menu li button {
  background: none;
  border: none;
  color: inherit;
  font-family: inherit;
  cursor: pointer;
  text-align: left;
  width: 100%;
}

.modal-main {
  flex-grow: 1;
  padding: 2rem;
  background: rgba(0, 0, 0, 0.6);
  color: #f3e9d2;
}

.post-section {
  padding: 1rem;
  flex: 1;
  overflow-y: auto;
}

.post-content article {
  margin-bottom: 2rem;
  border-bottom: 1px solid #ccc;
}

/* Responsive layout for smaller screens */
@media (max-width: 768px) {
  .modal-content {
    flex-direction: column;
    width: 95vw;
    padding: 1rem;
  }

  .modal-menu {
    width: 100%;
    font-size: 1.2rem;
    padding: 1rem;
  }

  .modal-main {
    padding: 1rem;
  }

  .book-img,
  .book-img-hidden {
    width: 80vw;
  }

  .grimoire-title {
    font-size: 1.5rem;
    top: 1rem;
  }
}
